class FilterUtils {
    static FilterTable = (row: any, tableDataFilter: { [key: string]: string }) => {
        let keys = Object.keys(row)
        for (let indexKey = 0; indexKey < keys.length; indexKey++) {
            const key = keys[indexKey]
            if (tableDataFilter[key]) {
                if (row != undefined && row[key] != null) {
                    if (tableDataFilter[key] !== "") {
                        if (
                            row[key]
                                .toString()
                                .toLowerCase()
                                .includes(tableDataFilter[key].toString().toLowerCase()) == false
                        ) {
                            return false
                        }
                    }
                } else {
                    return false
                }
            }
        }
        return true
    }

    static SortingTable = (a: any, b: any, sortChoosed: string) => {
        if (sortChoosed != null && sortChoosed != undefined) {
            if (a[sortChoosed] !== undefined && b[sortChoosed] !== undefined) {
                if (typeof a[sortChoosed] === 'string' && typeof b[sortChoosed] === 'string') {
                    return a[sortChoosed].localeCompare(b[sortChoosed])
                } else if (typeof a[sortChoosed] === 'number' && typeof b[sortChoosed] === 'number') {
                    return a[sortChoosed] - b[sortChoosed]
                }
            }
        }
        return 0
    }

    static FastIsEmpty = (arr: any[]) => {
        for (const _ in arr) {
            return false
        }
        return true
    }
}

export default FilterUtils